import { Text } from "@codemirror/state"
import { NodeProp } from "@lezer/common";
import { NodeBlueprint } from "./blueprint"
import { MovableSyntaxNode, GroupedNodeList, ASTNodeDescriptor } from "../structures/internal_structures/nodes/nodes"



export class NodeGenerator {
    constructor(
        private node_blueprint: NodeBlueprint,
        readonly sourceText: Text
    ) {}

    group(node: ASTNodeDescriptor) {
        const plan = this.node_blueprint.plans[node.name];
        return plan?.group ? plan.group(node.syntaxNodes, this) : [];
    }

    /**
     * Constructs an ASTNodeDescriptor from an MovableSyntaxNode object using the blueprint.
     * All groups associated with the source node are included in the classList of the resulting ASTNodeDescriptor
     * @param syntaxNode source
     * @returns an ASTNodeDescriptor object, or null if the syntaxNode type does not have an entry in the blueprint.
     */
    generateNode(syntaxNode: MovableSyntaxNode): ASTNodeDescriptor | null {
        const plan = this.node_blueprint.plans[syntaxNode.name];

        const syntaxNodes:GroupedNodeList = {}
        const sourceNodeTypes = plan ? plan.sourceNodeTypes : [syntaxNode.name];
        sourceNodeTypes.forEach(type => {
            syntaxNodes[type] = syntaxNode.name == type ? [syntaxNode] : syntaxNode.getChildren(type);
        });
        const classList = (plan?.classList || []).concat(syntaxNode.type.prop(NodeProp.group)||[]);
        return new ASTNodeDescriptor(syntaxNode.name, classList, syntaxNodes)
    }

    /**
     * Creates an ASTNodeDescriptor with the properties specified.
     * @param name the name of the node to be built
     * @param syntaxNodes the syntaxNodes from which this node is to be derived.
     * @returns an ASTNodeDescriptor object, or null if the blueprint does not have an entry for this node name.
     */
    constructNode(name: string, syntaxNodes:GroupedNodeList): ASTNodeDescriptor | null {
        const blueprint = this.node_blueprint.plans[name];
        if (!blueprint) return null;
        return new ASTNodeDescriptor(name, blueprint.classList||[], syntaxNodes);
    }
}