import { EditorState } from "@codemirror/state";
import { LinearParser } from "../../parsers/node_parser";
import { TabTree } from "../user_facing_structures/tree";
import { ASTFragmentCursor } from "../user_facing_structures/cursors/node_cursor";
import { NodeBlueprint } from "../../node_generation/blueprint";
import { ChangedRange, SyntaxNode } from "@lezer/common";
import { ASTNodeDescriptor, MovableSyntaxNode } from "./nodes/nodes";
import { NodeGenerator } from "../../node_generation/node-generator";

export abstract class Fragment {
    constructor(
        readonly from: number,
        readonly to: number,
    ) {}
    abstract get isParsed(): boolean;
    abstract createOffsetCopy(offset: number): Fragment;
    /**
     * Applies a set of edits to an array of fragments, reusing unaffected fragments,
     * removing fragments overlapping with edits, or creating new fragments with 
     * adjusted positions to replace fragments which have moved as a result of edits.
     * @param fragments a set of Fragment objects
     * @param changes a set of ChangedRanges representing edits
     * @returns a new set of fragments
     */
    static applyChanges(fragments: readonly Fragment[], changes: readonly ChangedRange[]) {
        if (!changes.length) return fragments;
        let result: Fragment[] = [];
        let fI = 1, nextF = fragments.length ? fragments[0] : null;
        for (let cI = 0, off=0;nextF; cI++) {
            let nextC = cI < changes.length ? changes[cI] : null;
            // TODO: be careful here with the <=. test to make sure that it should be <= and not just <.
            while (nextF && (!nextC || nextF.from <= nextC.toA)) {
                if (!nextC || nextF.to<=nextC.fromA) result.push(nextF.createOffsetCopy(-off));
                nextF = fI < fragments.length ? fragments[fI++] : null;
            }
            off = nextC ? nextC.toA - nextC.toB : 0;
        }
        return result;
    }

    /**
     * Create a set of fragments from a freshly parsed tree, or update
     * an existing set of fragments by replacing the ones that overlap
     * with a tree with content from the tree.
     * @param tree a freshly parsed tree
     * @param fragments a set of fragments
     * @returns fragment set produced by merging the tree's fragment set with the provided fragment set
     */
    static addTree(tree: TabTree, fragments: readonly Fragment[] = []) {
        let result = [...tree.fragments];
        for (let f of fragments) if (f.to > tree.to) result.push(f);
        return result
    }
    
}

export class ASTFragment extends Fragment {
    private constructor(
        readonly from: number,
        readonly to: number,
    ) { super(from, to) }
    private _nodeSet:ASTNodeDescriptor[] = []; 
    get nodeSet() { return this._nodeSet }
    advance(): ASTFragmentCursor | null {
        if (this.isBlankFragment) return ASTFragmentCursor.dud;
        const nodeSet = this.linearParser.advance();
        if (this.linearParser.isDone) {
            this._nodeSet = nodeSet;
            return new ASTFragmentCursor(this);
        }
        return null
    }

    private linearParser: LinearParser;

    /**
     * Creates an unparsed TabFragment object that can be incrementally parsed 
     * by repeatedly calling the TabFragment.advance() method.
     * @param node source node from which parsing begins
     * @param editorState the EditorState from which the syntaxNode was obtained
     * @param blueprint the blueprint used to create the ASTNodeDescriptor structure
     * @returns an unparsed TabFragment object
     */
    static startParse(node: SyntaxNode, editorState: EditorState, blueprint: NodeBlueprint): ASTFragment | null {
        if (!blueprint.anchors.has(node.name)) return null;
        const fragment = new ASTFragment(node.from, node.to)
        fragment.linearParser = new LinearParser(new MovableSyntaxNode(node, fragment.from), new NodeGenerator(blueprint, editorState.doc));
        return fragment;
    }

    createOffsetCopy(offset: number): ASTFragment {
        const copy = new ASTFragment(this.from+offset, this.to+offset);
        copy.linearParser = this.linearParser;
        return copy;
    }

    get isBlankFragment() { return !this.linearParser }
    static createBlankFragment(from: number, to: number) {
        return new ASTFragment(from, to);
    }

    get isParsed() { return this.isBlankFragment || this.linearParser!.isDone }
    toString() {
        return new ASTFragmentCursor(this).printTree() || "";
    }
    
}
